<template>
  <section class="container-fluid">
    <div class="d-flex justify-content-around mt-2 mb-2">
      <button v-if="buttonText !== 'Aktif Durumda'" class="btn btn-success" @click="startBot">
        {{ buttonText }}
      </button>
      <button :disabled="buttonText === 'Botu Başlat'" class="btn btn-danger" @click="pauseBot"><i
        class="bi bi-pause"></i> {{ pauseButtonText }}

      </button>
    </div>
    <div class="row mt-3">
      <p v-if="requestCount > 0" class="text-center">
        <!--        Şimdiye Kadar {{ requestCount }} işlem gerçekleştirildi.-->
        {{ buyboxCounter }} Buybox işlemi gerçekleştirildi.
        <br>
        Her işlem 10 saniye aralıklarla gerçekleşmektedir.
        <br>
        Son işlem: {{ new Date().toLocaleString() }}
        Sıradaki işlem: {{ new Date(Date.now() + 10000).toLocaleString() }}
      </p>
      <div class="col-12 border border-dark">
        <div class="row  d-flex justify-content-center mt-2 mb-2">
          <div class="col-6">
            <p>{{index + 1}}. Ürün</p>
            <ul class="list-group">
              <li class="list-group-item list-group-item-info">Barkod:<br/> {{ item.barcode }}</li>
              <li class="list-group-item list-group-item-warning">Piyasa Satış Fiyatı:<br/> {{ item.listPrice }}</li>
              <li class="list-group-item list-group-item-success">Trendyol Satış Fiyatı:<br/> {{ item.salePrice }}</li>
              <li class="list-group-item list-group-item-primary">Stok:<br/> {{ item.quantity }}</li>
            </ul>
          </div>
          <div class="col-6">
            <ul style="height: 250px;overflow-y: auto">
              <li v-for="log in logs" :key="log">{{ log }}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  methods: {
    // 3 ' den geriye doğru sayan bir method
    countDown () {
      this.second = 10
      const interval = setInterval(() => {
        this.buttonText = `Bot Başlatılıyor ${this.second}`
        if (this.second <= 0) {
          clearInterval(interval)
          this.buttonText = 'Aktif Durumda'
        }
        this.second--
      }, 1000)
    },

    buyBoxProcess (buyboxCounter) {
      let items = [{
        barcode: this.item.barcode,
        quantity: this.item.quantity,
        salePrice: this.item.salePrice,
        listPrice: this.item.listPrice
      }]
      let log = ''
      if (buyboxCounter === 0) {
        items = [
          {
            barcode: this.item.barcode,
            quantity: 0,
            salePrice: this.item.salePrice,
            listPrice: null
          }
        ]
        log = `${buyboxCounter + 1}. işlem ---- Stok ${items[0].quantity} olarak güncellendi! & T.S.E ${items[0].salePrice} olarak güncellendi!`
      } else if (buyboxCounter === 1) {
        items[0].quantity = 0
        items[0].salePrice = this.item.salePrice + 0.40
        items[0].listPrice = null
        log = `${buyboxCounter + 1}. işlem ---- Stok ${items[0].quantity} olarak güncellendi! & T.S.E ${items[0].salePrice} olarak güncellendi!`
      } else {
        items[0].quantity = this.item.quantity
        items[0].salePrice = null
        items[0].listPrice = null
        log = `${buyboxCounter + 1}. işlem ---- Stok ${items[0].quantity} olarak güncellendi! & T.S.E ${items[0].salePrice} olarak güncellendi!`
      }
      this.logItem = items[0]
      return {
        items,
        log
      }
    },
    requestPut (items, log) {
      const url =
        'https://sellerpublic-sdc.trendyol.com/product-spm-sellersfxspm-santral/price-and-inventory?culture=tr-TR&storeFrontCode=TR'
      const apiKey = this.$store.getters.getJwt

      const data = {
        items: items
      }

      const options = {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${apiKey}` // Include API key in Authorization header
        },
        body: JSON.stringify(data)
      }

      fetch(url, options)
        .then((response) => {
          // this.logs.push(`${new Date().toLocaleString()} - 'İstek Gönderiliyor...'`)
          console.log('güncellendi! => ', response)
          // this.logs.push(`${new Date().toLocaleString()} - Ürün Stoğu : ${items[0].quantity} --- Ürün T.S.E : ${items[0].salePrice} olarak güncellendi!`)
          this.logs.push(log)
          this.requestCount++
        })
        .catch((error) => {
          console.error('Hata:', error)
          this.logs.push(`${new Date().toLocaleString()} - error: ${error}`)
        })
    },

    startBot () {
      this.buttonText = 'Çalışıyor'
      this.countDown()
      let buyBoxCounter = 0 // Change const to let
      this.interval = setInterval(() => {
        const {
          items,
          log
        } = this.buyBoxProcess(buyBoxCounter)
        switch (buyBoxCounter) {
          case 0:
            buyBoxCounter = 1 // Update the value of buyBoxCounter
            this.requestPut(items, log)
            break
          case 1:
            buyBoxCounter = 2 // Update the value of buyBoxCounter
            this.requestPut(items, log)
            break
          case 2:
            buyBoxCounter = 0 // Update the value of buyBoxCounter
            this.requestPut(items, log)
            setTimeout(() => {
              this.pauseBot()
              this.logs.push('Buybox kontrolü tamamlandı! - Yeni Buybox kontrolü 3-4 dakika içinde başlayacak!')
            }, 1500)
            break
        }
        if (this.logs.length >= 10) {
          this.logs = []
        }
      }, 10000)
    },
    pauseBot () {
      this.buttonText = 'Botu Başlat'
      this.logs.push(`${new Date().toLocaleString()} - Bot durduruldu!`)
      this.second = 0
      clearInterval(this.interval)
    }
  },
  mounted () {
    if (this.item.autoStart) {
      this.startBot()
    }
  },

  data () {
    return {
      buttonText: 'Botu Başlat',
      pauseButtonText: 'Durdur',
      requestCount: 0,
      logs: [],
      interval: null,
      second: 0,
      logItem: null
    }
  }
}
</script>
